@import '../../styles/colors';
@import '../../styles/fonts';

.bkKeeping {
  z-index: -1;
  height: 1100px;
  position: relative;
  max-width: 100%;
  background-color: $gray;
  padding-top: 200px;
  margin-top: -100px;

  &__title {
    display: flex;
    flex-direction: column;
    align-items: center;

    h1 {
      font-size: 32px;
      font-weight: $bolt;
      line-height: 66px;
    }

    p {
      margin-top: 15px;
      text-align: center;
      font-size: 16px;
      line-height: 28px;
      font-weight: $light;
      width: 76%;
    }
  }

  &__body {
    max-width: 1108px;
    margin: 0 auto;
    display: flex;
    color: $black;

    .text {
      padding-left: 102px;

      &__title {
        display: flex;
        align-items: center;
        color: $purple;

        h2 {
          margin-left: 36px;
          font-size: 40px;
          line-height: 32px;
          font-weight: $bolt;
        }
      }

      &__btn {
        background-color: $purple;
        margin-top: 42px;
        width: 259px;
        height: 70px;
        font-size: 22px;
        line-height: 66px;
        color: white;
        font-weight: $bolt;
      }

      p {
        margin-top: 50px;
        font-size: 16px;
        line-height: 25px;
        width: 95%;
      }
    }
  }
}

.bkKeeping::after {
  content: '';
  position: absolute;
  z-index: -1;
  top: -480px;
  width: 100%;
  height: 480px;
  background: #f4f4f4;
  clip-path: polygon(133% -23%, 100% 0%, 100% 100%, 0% 100%);
}
